@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Spinnaker&display=swap);
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  border: 1px solid #1b1b1b;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #fff;
  border-radius: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  scroll-behavior: smooth;
  text-decoration: none;
}

iframe {
  width: 100%;
  height: 320vh;
  margin-top: -185vh;
}

body,
html,
#root {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  background-color: #1b1b1b;
  font-size: 16px;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 991px) {
  body,
  html,
  #root {
    font-size: 15px;
  }
}

@media only screen and (max-width: 480px) {
  body,
  html,
  #root {
    font-size: 14px;
  }
}


#vireSeuTelefone {
    display:none;
}

@media (max-width: 920px) and (orientation:landscape) {

    #vireSeuTelefone {
        display:inline;
        display:initial;
        position: absolute;
        top: 0;
        left:0;
        width: 100vw;
        height: 100vh; 
        z-index:100;
    }
  }

.radio{
    width: 15px;
    height: 25px;
    border-radius: 0px;
    
}

.radio::-ms-check  {
    color: red;
    background: black;
    padding: 1em;
}

input[type='radio'] {
    -webkit-appearance: none;
    appearance: none;

    border-radius: 50%;
    width: 18px;
    height: 18px;

    border: 1px solid black;
    transition: 0.2s all linear;
    margin-right: 5px;

    position: relative;
    top: 1px;
    }

    input[type='radio'] {
    -webkit-appearance: none;
    appearance: none;  
    width: 18px;
    height: 18px;
    border: 1px solid white;
    transition: 0.2s all linear;
    margin-right: 5px;
    position: relative;
    top: 1px;
    }

    input[type='radio']:checked, input[type='radio']:checked {
      border: 1px solid black;
      background: #FDCC12;
    }
